/*----------------------------------------*/
/*  01. Theme default CSS
/*----------------------------------------*/

/*-- Google Font --*/
// @import url('https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900');

/*-- Common Style --*/
*,
*::after,
*::before {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
body {
  background-color: $body-bg-2;
  line-height: 23px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  visibility: visible;
  font-family: $body-font;
  color: $body-color-2;
  position: relative;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $heading-color;
  font-family: $heading-font;
  margin-top: 0;
}
h1 {
  font-size: 48px;
  line-height: 55px;
}
h2 {
  font-size: 36px;
  line-height: 36px;
}
h3 {
  font-size: 24px;
  line-height: 30px;
}
h4 {
  font-size: 20px;
  line-height: 28px;
}
h5 {
  font-size: 16px;
  line-height: 20px;
}
h6 {
  font-size: 14px;
  line-height: 18px;
}

@media #{$large-mobile} {
  h1 {
    font-size: 34px;
    line-height: 42px;
    & br {
      display: none;
    }
  }
}
@media #{$small-mobile} {
  h1 {
    font-size: 26px;
    line-height: 32px;
    & br {
      display: none;
    }
  }
}
p {
  font-size: 14px;
}
p:last-child {
  margin-bottom: 0;
}
a,
button {
  color: inherit;
  display: inline-block;
  line-height: inherit;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  color: $theme-color;
}
a,
button,
img,
input,
span {
  transition: all 0.3s ease 0s;
}
*:focus {
  outline: none !important;
}
a:focus {
  color: inherit;
  outline: none;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
button,
input[type='submit'] {
  cursor: pointer;
}
ul {
  list-style: outside none none;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
}
button:focus,
a:focus {
  outline: none;
  box-shadow: none;
}
.btn,
.btn:focus {
  outline: 0;
  box-shadow: none !important;
}
.header-sticky {
  &.is-sticky {
    position: fixed;
    width: 100%;
    z-index: 999999999;
    top: 0;
  }
  &.bg-white {
    &.is-sticky {
      background: #fff;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
      position: fixed;
      width: 100%;
      z-index: 999999999;
      top: 0;
    }
  }
}
