/*--
    Scroll Up
-----------------------------------------*/
#scrollUp {
  width: 40px;
  height: 40px;
  background-color: $theme-color;
  border: 3px solid #eeeeee;
  color: $white;
  right: 20px;
  bottom: 60px;
  text-align: center;
  overflow: hidden;
  border-radius: 50px;
  z-index: 9811 !important;
  @media #{$large-mobile} {
    display: none !important;
  }
  & i {
    display: block;
    line-height: 34px;
    font-size: 16px;
  }
  &:hover {
    & i {
      animation-name: fadeInUp;
      animation-duration: 1s;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
    }
  }
}

