/*-- Tab Content & Pane Fix --*/
.tab-content {
  width: 100%;
  & .tab-pane {
    display: none;
    &.active {
      display: block;
    }
    /* Support for Bootstrap fade transitions */
    &.fade {
      transition: opacity 0.15s linear;
      &:not(.show) {
        opacity: 0;
      }
      &.show {
        opacity: 1;
      }
    }
  }
}

/*--
    - Product Full Width
------------------------------------------*/
.full-width-product-wrap {
  & > [class*='col-'],
  & > [class*='col'] {
    &:nth-child(1) {
      flex: 1 0 450px;
      max-width: 450px;

      // Responsive
      @media #{$laptop-device} {
        flex: 1 0 350px;
        max-width: 350px;
      }
      @media #{$desktop-device} {
        flex: 1 0 350px;
        max-width: 350px;
      }
      @media #{$tablet-device} {
        flex: 0 0 100%;
        max-width: 100%;
      }
      @media #{$large-mobile} {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
    &:nth-child(2) {
      flex: 0 0 calc(100% - 450px);
      max-width: calc(100% - 450px);

      // Responsive
      @media #{$laptop-device} {
        flex: 0 0 calc(100% - 350px);
        max-width: calc(100% - 350px);
      }
      @media #{$desktop-device} {
        flex: 0 0 calc(100% - 350px);
        max-width: calc(100% - 350px);
      }
      @media #{$tablet-device} {
        flex: 0 0 100%;
        max-width: 100%;
      }
      @media #{$large-mobile} {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }

  &.desktop-full-width {
    & > [class*='col-'],
    & > [class*='col'] {
      &:nth-child(1) {
        // Responsive
        @media #{$desktop-device} {
          flex: 0 0 100%;
          max-width: 100%;
        }
      }
      &:nth-child(2) {
        // Responsive
        @media #{$desktop-device} {
          flex: 0 0 100%;
          max-width: 100%;
        }
      }
    }
  }
}

.laptop-3-column {
  & > [class*='col-'],
  & > [class*='col'] {
    // Responsive
    @media #{$laptop-device} {
      flex: 1 0 33.3333%;
