/*--
    - Button
------------------------------------------*/
.btn,
.btn-2 {
  border: 1px solid #626262;
  line-height: 24px;
  padding: 10px 40px;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
  color: $heading-color;
  overflow: hidden;
  position: relative;
  z-index: 1;
  transition: all 0.3s ease 0s;

  &:focus {
    outline: none;
  }

  // Before & After Selector
  &::before {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 0;
    width: 150%;
    background-color: $heading-color;
    transition: all 0.3s ease 0s;
    content: '';
    transform: translateX(-50%) translateY(-50%) rotate(-30deg);
    opacity: 0;
    z-index: -1;
  }

  // Hover
  &:hover {
    color: $theme-color;

    &::before {
      height: 300%;
      opacity: 1;
    }
  }

  // Hover
  &.hover-theme {
    // Before & After Selector
    &::before {
      background-color: $theme-color;
    }
    &:hover {
      border-color: $theme-color;
      color: $heading-color;

      &::before {
        height: 300%;
        opacity: 1;
      }
    }
  }

  &.btn-circle {
    border-radius: 50px;
  }
  &.btn-radius {
    border-radius: 5px;
  }
  &.btn-square {
    border-radius: 0;
  }

  &.btn-text {
    border-radius: 0;
    padding: 0;
    line-height: 20px;
    border: none;
    &::before {
      display: none;
    }
    // Hover
    &.hover-theme {
      &:hover {
        color: $theme-color;
      }
    }
  }
  &.btn-underline {
    text-decoration: underline;
  }

  &.btn-large {
    padding: 15px 40px;
    font-size: 16px;

    // Responsive
    @media #{$large-mobile} {
      font-size: 12px;
      padding: 7px 25px;
    }
    @media #{$small-mobile} {
      font-size: 11px;
      padding: 4px 15px;
      margin-top: 15px;
    }
  }
  &.btn-medium {
    padding: 7px 20px;
    font-size: 12px;

    // Responsive
    @media #{$large-mobile} {
      font-size: 12px;
      padding: 7px 25px;
    }
    @media #{$small-mobile} {
      font-size: 11px;
      padding: 4px 15px;
      margin-top: 15px;
    }
  }
  &.btn-small {
    padding: 4px 15px;
    font-size: 11px;
  }

  // Responsive
  @media #{$large-mobile} {
    font-size: 12px;
    padding: 7px 25px;
  }
  @media #{$small-mobile} {
    font-size: 11px;
    padding: 4px 15px;
  }
}
.btn-2 {
  padding: 8px 30px;
}

