    margin-bottom: 20px;
    padding-top: 10px;
    overflow: hidden;

    &:last-child {
      margin-bottom: 0;
    }
  }
  & .single-related-image {
    position: absolute;
    left: 0;
    top: 5px;
    overflow: hidden;
    max-width: 100px;
  }
  & .single-related-contents {
    & h5 {
      font-weight: 600;
      font-size: 16px;
    }
    & p {
      font-weight: 13px;
    }
  }
}
.archives {
  & li {
    position: relative;
    padding-left: 10px;
    margin-bottom: 10px;
    font-weight: 500;
    &::before {
      position: absolute;
      content: '';
      left: 0;
      top: 50%;
      background: #222;
      height: 3px;
      width: 3px;
      border-radius: 50%;
    }
    & a {
      & span {
        margin-left: 30px;
      }
    }
  }
}

.section-title {
  & h4 {
    margin-bottom: 0;
  }
}

.justify-content-between {
  & [class*='col'],
  & [class*='col-'] {
    flex-grow: 0;
  }
}
.black-ovelay {
  position: relative;
  &::before {
    background: rgba(0, 0, 0, 0.8);
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
  }
}
/*-- Overlay styles --*/
[data-overlay],
[data-secondary-overlay],
[data-black-overlay],
[data-white-overlay] {
  position: relative;
}

[data-overlay] > div,
[data-overlay] > *,
[data-secondary-overlay] > div,
[data-secondary-overlay] > *,
[data-black-overlay] > div,
[data-black-overlay] > *,
[data-white-overlay] > div,
[data-white-overlay] > * {
  position: relative;
  z-index: 2;
}

[data-overlay]:before,
[data-secondary-overlay]:before,
[data-black-overlay]:before,
[data-white-overlay]:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
}

[data-overlay]:before {
  background-color: $theme-color;
}

[data-secondary-overlay]:before {
  background-color: #6564db;
}

[data-black-overlay]:before {
  background-color: #000000;
}

[data-white-overlay]:before {
  background-color: #ffffff;
}

@for $i from 1 through 10 {
  [data-overlay='#{$i}']:before,
  [data-secondary-overlay='#{$i}']:before,
  [data-black-overlay='#{$i}']:before,
  [data-white-overlay='#{$i}']:before {
    opacity: #{$i * 0.1};
  }
}
