/*-- Common Style --*/
*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background-color: #242424;
  line-height: 23px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  visibility: visible;
  font-family: "Rubik", sans-serif;
  color: #ffffff;
  position: relative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #3a3a3a;
  font-family: "Rubik", sans-serif;
  margin-top: 0;
}

h1 {
  font-size: 48px;
  line-height: 55px;
}

h2 {
  font-size: 36px;
  line-height: 36px;
}

h3 {
  font-size: 24px;
  line-height: 30px;
}

h4 {
  font-size: 20px;
  line-height: 28px;
}

h5 {
  font-size: 16px;
  line-height: 20px;
}

h6 {
  font-size: 14px;
  line-height: 18px;
}

@media only screen and (max-width: 767px) {
  h1 {
    font-size: 34px;
    line-height: 42px;
  }
  h1 br {
    display: none;
  }
}
@media only screen and (max-width: 479px) {
  h1 {
    font-size: 26px;
    line-height: 32px;
  }
  h1 br {
    display: none;
  }
