------------------------------------------*/
.btn,
.btn-2 {
  border: 1px solid #626262;
  line-height: 24px;
  padding: 10px 40px;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
  color: #3a3a3a;
  overflow: hidden;
  position: relative;
  z-index: 1;
  transition: all 0.3s ease 0s;
}
.btn:focus,
.btn-2:focus {
  outline: none;
}
.btn::before,
.btn-2::before {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 0;
  width: 150%;
  background-color: #3a3a3a;
  transition: all 0.3s ease 0s;
  content: "";
  transform: translateX(-50%) translateY(-50%) rotate(-30deg);
  opacity: 0;
  z-index: -1;
}
.btn:hover,
.btn-2:hover {
  color: #c89979;
}
.btn:hover::before,
.btn-2:hover::before {
  height: 300%;
  opacity: 1;
}
.btn.hover-theme::before,
.btn-2.hover-theme::before {
  background-color: #c89979;
}
.btn.hover-theme:hover,
.btn-2.hover-theme:hover {
  border-color: #c89979;
  color: #3a3a3a;
}
.btn.hover-theme:hover::before,
.btn-2.hover-theme:hover::before {
  height: 300%;
  opacity: 1;
}
.btn.btn-circle,
.btn-2.btn-circle {
  border-radius: 50px;
}
.btn.btn-radius,
.btn-2.btn-radius {
  border-radius: 5px;
}
.btn.btn-square,
.btn-2.btn-square {
  border-radius: 0;
}
.btn.btn-text,
.btn-2.btn-text {
  border-radius: 0;
  padding: 0;
  line-height: 20px;
  border: none;
}
